.text-center {
  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #ffffff;
  height: 100vh;
}

p {
  color: #000000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #262626;
  font-size: 2rem;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  font-size: 3rem;
  color: #ffffff;
  margin: 0%;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

nav {
  width: 100vw;
  display: grid;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  box-shadow: 1px 1px 5px #262626;
  background-color: #0367A6;
  color: #ffffff;
}

.navegacion {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.navegacion .logo__container {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.navegacion .logo__container img {
  max-width: 4rem;
}
.navegacion .nav__items {
  display: flex;
  gap: 2rem;
}
.navegacion .nav__items a {
  color: #ffffff;
  font-size: 1.8rem;
  padding: 0 2rem;
}
.navegacion .nav__items a:hover {
  border-bottom: 3px solid #397774;
}
.navegacion .nav__signin-signup {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.navegacion .nav__signin-signup a:first-child {
  color: #ffffff;
}
.navegacion .nav__signin-signup a:first-child:hover {
  transform: scale(1.2);
  z-index: 100;
}
.navegacion .btn {
  display: none;
}

.btn-primario {
  background-color: #0367A6;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 1rem;
}
.btn-primario:hover {
  cursor: pointer;
  background-color: #024874;
}

.btn-secundario {
  background-color: #397774;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border-radius: 1rem;
}
.btn-secundario:hover {
  cursor: pointer;
  background-color: #285552;
}
.btn-secundario:hover {
  color: #ffffff;
}

.btn {
  border: none;
  font-size: 4rem;
  background-color: transparent;
}